import { buttonFilled, itemClass, itemMenu } from '../templates/components';
import {
  iconClassroom, iconCircledPerson, iconStudents, iconCheckMark,
  iconNote,
  iconGraduate,
} from '../templates/icons';

class Home extends HTMLElement {
  connectedCallback() {
    this.render();
    this.insertMenu();
    this.inserClassroom();
    // this.handler();
  }

  render() {
    this.innerHTML = `
      <div id="home-container" class="m-5 lg:w-2/5 lg:mx-auto">
        <div class="flex justify-between items-center mb-5">
          <h1 class="text-2xl font-bold">SM App</h1>
          <span>${iconCircledPerson}</span>
        </div>
        <div id="container-menu" class="flex flex-wrap items-center gap-4 mb-6"></div>
        ${buttonFilled('create-class', 'Buat Kelas', 'mb-6', 'classroom-form')}
        <div class="mb-5">
          <h2 class="text-xl mb-4">Ruang Kelas</h2>
          <div id="container-classroom" class="flex flex-wrap gap-3"></div>
        </div>

        <div id="container-nav-page" class="flex justify-between"></div>
        <div id="container-load"></div>
      </div>
    `;
  }

  insertMenu() {
    const containerMenu = this.querySelector('#container-menu');
    containerMenu.innerHTML += itemMenu({
      id: 'menu-classroom',
      href: '/#container-classroom',
      icon: iconClassroom,
      count: 7,
      unit: 'Ruang',
      title: 'Kelas',
      isNav: false,
    });
    containerMenu.innerHTML += itemMenu({
      id: 'menu-closed-class',
      href: '/#',
      icon: iconCheckMark(),
      count: 3,
      unit: 'Kelas',
      title: 'Ditutup',
      isNav: true,
    });
    containerMenu.innerHTML += itemMenu({
      id: 'menu-students',
      href: '/#/students',
      icon: iconStudents,
      count: 84,
      unit: 'Orang',
      title: 'Siswa',
      isNav: true,
    });
    containerMenu.innerHTML += itemMenu({
      id: 'menu-alumni',
      href: '/#',
      icon: iconGraduate,
      count: 49,
      unit: 'Orang',
      title: 'Alumni',
      isNav: true,
    });
    containerMenu.innerHTML += itemMenu({
      id: 'menu-notes',
      href: '/#',
      icon: iconNote,
      count: 12,
      unit: 'Lembar',
      title: 'Catatan',
      isNav: true,
    });
  }

  inserClassroom() {
    const containerClassroom = this.querySelector('#container-classroom');
    containerClassroom.innerHTML += itemClass({
      id: 1,
      kelas: 'X-A',
      jurusan: 'TITL',
    });
    containerClassroom.innerHTML += itemClass({
      id: 2,
      kelas: 'X-B',
      jurusan: 'TITL',
    });
    containerClassroom.innerHTML += itemClass({
      id: 3,
      kelas: 'XI',
      jurusan: 'TITL',
    });
    containerClassroom.innerHTML += itemClass({
      id: 4,
      kelas: 'XII',
      jurusan: 'TITL',
    });
  }

  // handler() {
  //   const homeContainer = this.querySelector('#home-container');
  //   const containerLoad = this.querySelector('#container-load');
  //   const requestOnlyComponent = `
  //     <div class="relative flex gap-x-3 mb-5">
  //       <div class="flex h-6 items-center">
  //         <input id="req-only" name="req-only" type="checkbox" class="h-4 w-4 rounded accent-[#3572EF] cursor-pointer">
  //       </div>
  //       <label for="req-only" class="text-[#3572EF]">Request only</label>
  //     </div>
  //   `;
  //   // HomeHandler.init({
  //   //   homeContainer,
  //   //   changeRoleBtn,
  //   //   homeUserRole,
  //   //   username,
  //   //   containerActionPayment,
  //   //   containerRequestOnly,
  //   //   month,
  //   //   year,
  //   //   requestOnlyComponent,
  //   //   containerLoad,
  //   // });
  // }
}

customElements.define('home-page', Home);
const home = '<home-page></home-page>';
export default home;
